import math from './math'

//TODO: make this a constant somewhere
const NEAR_COORDS = [[-1,0],[1,0],[0,-1],[0,1]]

export const ENTITY = {
  PLAYER: 1,
  BOX: 2,
  HEAVY_BOX: 3,
  VOID: 4,
  GRUE: 10,
  SNAKE: 11,
  ZOMBIE: 12,
  EGG: 20,
}

const Movable = { isMovable: true }
const NotMovable = { isMovable: false }

const Monster = { isMonster: true }

const Squashable = {
  isSquashable: true,
  squash: function() {
    console.log('squash',this)
    this.kill()
  }
}

const Killable = {
  isKillable: true,
  kill: function(score) {
    if (this.isDead) {
      console.error('entitity already dead in .kill()',this)
      return
    }

    this.isDead = true

    // remove ourselfs from the map so we can move another entity
    // to this position the same tick (ie 'squash')
    this.game.map.remove(this)

    this.game.publish('kill',this,score || this.score)
  }
}

const Trappable = {
  checkIfTrapped: function(step) {
    const map = this.game.map

    // find any free coords
    let trapped = true
    for (let i=0; i < NEAR_COORDS.length; i++) {
      let x = this.x + NEAR_COORDS[i][0]
      let y = this.y + NEAR_COORDS[i][1]
      if (map.isWithin(x,y) && (map.isEmpty(x,y) || map.isPlayer(x,y))) {
        trapped = false
        break
      }

      // TODO: handle groups of trapped monsters
      let e = map.entityAt(x,y)
      if (e && e.isMonster) {
        trapped = false
        break
      }
    }

    if (!trapped) {
      this.isTrapped = false
      this.trappedFor = 0
      return false
    }

    if (!this.isTrapped) console.log("uhu, I'm trapped!",this)

    this.isTrapped = true
    this.trappedFor = this.trappedFor || 0
    this.trappedFor += step
    return true
  }
}


//TODO: game passed in here smells, think about how we could decouple this?
export function createEntity(type, x, y, game) {
  switch(type) {
    case ENTITY.BOX: return Object.assign( {x,y,game}, Box,  Movable)
    case ENTITY.HEAVY_BOX: return Object.assign( {x,y,game}, Box, NotMovable)
    case ENTITY.VOID: return Object.assign( {x,y,game}, Void, NotMovable)
    case ENTITY.SNAKE:  return Object.assign({x,y,game}, Snake,  NotMovable, Monster, Killable, Squashable)
    case ENTITY.GRUE:   return Object.assign({x,y,game}, Grue,   NotMovable, Monster, Killable, Squashable, Trappable)
    case ENTITY.ZOMBIE: return Object.assign({x,y,game}, Zombie, NotMovable, Monster, Killable, Squashable, Trappable)
    case ENTITY.EGG: return Object.assign({x,y,game}, Egg, NotMovable, Killable, Squashable)
  }
  throw new Error('createEntity for unknown type '+type)
}

// -- Entities --
//TODO: move to modules

const Void = {
  type: ENTITY.VOID,
  name: 'Void',
}

const Box = {
  type: ENTITY.BOX,
  name: 'Box',
}


// Snakes waits on a tile and attacks player when near
const Snake = {
  type: ENTITY.SNAKE,
  name: 'Snake',
  score: 100,

  update: function(step) {
    const map = this.game.map

    if (this.isDead) return

    if (this.think) {
      this.think = this.think - step
      if (this.think > 0) return
    }

    // trapped?

    // attack?
    if (map.nextTo(this,map.player)) {
      this.game.publish('attack',this,map.player)
      this.think = 2
      return
    }
  }
}

// TODO: rename to xxx
const Egg = {
  type: ENTITY.EGG,
  name: 'Egg',
  score: 200,
  period: 10,
  spawnEntityOfType: ENTITY.GRUE,

  update: function(step) {
    if (this.isDead) return

    this.nextSpawn = this.nextSpawn || this.period
    this.nextSpawn -= step

    // waiting?
    if (this.nextSpawn > 0) return

    // spawn
    console.log('trying to spawn monster!')
    const map = this.game.map

    for (let i=0; i < NEAR_COORDS.length; i++) {
      let x = this.x + NEAR_COORDS[i][0]
      let y = this.y + NEAR_COORDS[i][1]

      if (map.isWithin(x,y) && map.isEmpty(x,y)) {
        let m = createEntity(this.spawnEntityOfType,x,y,this.game)
        map.monsters.push(m)
        map.tiles[map.posToIndex(m.x,m.y)] = m
        console.log('spawned ',m.name,'at',m.x,m.y)
        break
      }
    }

    // reset
    this.nextSpawn = this.period
  }
}

const Grue = {
  type: ENTITY.GRUE,
  name: 'Grue',
  score: 500,
  sleep: 1,

  update: function (step) {
    const map = this.game.map

    if (this.isDead) return

    // TODO: rename this.sleep to this.think
    // dont bother trying to update a monster that is still 'thinking'
    // if (this.thinking && --this.thinking)
    //   return;
    if (this.sleep) {
      this.sleep = this.sleep - step
      if (this.sleep > 0) return
    }

    // trapped?
    if (this.checkIfTrapped(step)) {
      if (this.trappedFor < 2) return
      this.game.publish('explosion',{x: this.x, y: this.y})
      this.kill(this.score * 5)
      return
    }

    // attack?
    if (map.nextTo(this,map.player)) {
      this.game.publish('attack',this,map.player)
      this.sleep = 1
      return
    }

    // -- Move Grue--

    // Grues find their victims by smell.
    // To model this we use pathfinding with a max path length
    this.path = map.pathToPlayer(this)
    if (this.path.length > 0 && this.path.length < 10) {
      console.log('I smell a player',this.path.length)
      if (map.tryMove(this,this.path[1].x,this.path[1].y)) {
        map.move(this,this.path[1].x,this.path[1].y)
      }
      this.sleep = 1
      return
    }

    // What do grues do when they idle?
    if (math.random(0,100) < 20) {
      map.moveRandom(this)
      this.sleep = 1
      return
    }

    // some default think time
    this.sleep = 1
  }
}


const Zombie = {
  type: ENTITY.ZOMBIE,
  name: 'Zombie',
  score: 300,
  sleep: 1,

  update: function (step) {
    const map = this.game.map

    if (this.isDead) return

    // TODO: rename this.sleep to this.think
    // dont bother trying to update a monster that is still 'thinking'
    // if (this.thinking && --this.thinking)
    //   return;
    if (this.sleep) {
      this.sleep = this.sleep - step
      if (this.sleep > 0) return
    }

    // trapped?
    if (this.checkIfTrapped(step)) {
      if (this.trappedFor < 2) return
      this.game.publish('explosion',{x: this.x, y: this.y})
      this.kill(this.score * 5)
      return
    }

    // attack?
    if (map.nextTo(this,map.player)) {
      this.game.publish('attack',this,map.player)
      this.sleep = 1
      return
    }

    // -- Move Zombie --

    // Zombies find ther victims by sight.
    // We use map.canSeePlayer wich uses a simple line algo
    this.path = map.canSeePlayer(this,10)
    if (this.path) {
      console.log('I can see a player',this.path.length)
      if (map.tryMove(this,this.path[1].x,this.path[1].y)) {
        map.move(this,this.path[1].x,this.path[1].y)
      }
      this.sleep = 1
      return
    }

    // What do zombies do when they idle?
    // TODO: move in circle
    if (math.random(0,100) < 20) {
      map.moveRandom(this)
      this.sleep = 1
      return
    }

    // some default think time
    this.sleep = 1
  }
}



