import {ENTITY,createEntity} from './entities'
import math from './math'
import { generate } from './level-generator'

const WIDTH = 30
const HEIGHT = 19

// Iphone 4 640x960 (320x480 16x16 = 20*30)
// Iphone 5 640x1136 (320x568 16x16 = 20*35.5 or x margin of 88)

const sourceToEntity = {
  '.' : false,
  '@' : ENTITY.PLAYER,
  'b' : ENTITY.BOX,
  'B' : ENTITY.HEAVY_BOX,
  ',' : ENTITY.VOID,
  'G' : ENTITY.GRUE,
  'S' : ENTITY.SNAKE,
  'Z' : ENTITY.ZOMBIE,
  'E' : ENTITY.EGG,
}

// TODO: remove
export function posToIndex (x,y) {
  return (y*WIDTH) + x
}

export function indexToPos (index) {
  let y = Math.floor(index / WIDTH)
  let x = index - (y * WIDTH)
  return [x,y]
}

// Mixin
export default {
  loaded: false,

  posToIndex : posToIndex,
  indexToPos: indexToPos,

  entityAt: function (x,y) { return this.tiles[posToIndex(x,y)] },
  isEmpty: function(x,y) { return this.entityAt(x,y) === false },
  isWithin: function(x,y) { return x > -1 && x < this.width && y > -1 && y < this.height },
  isPlayer: function(x,y) { return this.player.x === x && this.player.y === y },

  near: function({x,y},{x : x1,y : y1}) {
    return (((x == x1) || (x == x1 - 1) || (x == x1 + 1)) &&
           ((y == y1) || (y == y1 - 1) || (y == y1 + 1)))
  },

  nextTo: function({x,y},{x : x1,y : y1}) {
    if (x == x1 && y == y1 - 1) return true
    if (x == x1 && y == y1 + 1) return true
    if (x == x1 - 1 && y == y1) return true
    if (x == x1 + 1 && y == y1) return true
    return false
  },

  move: function(entity,x,y) {
    //if (!tryMove(entity,x,y)) return false
    if (this.entityAt(x,y)) {
      console.error('move to bad coords', entity, x ,y)
    }

    this.tiles[posToIndex(entity.x,entity.y)] = false
    this.tiles[posToIndex(x,y)] = entity

    entity.x = x
    entity.y = y
  },

  tryMove: function(entity,x,y) {
    const other = this.entityAt(x,y)
    if (!other && this.isWithin(x,y)) {
      return true
    } else {
      return false
    }
  },

  _randomCoords: [[-1,0],[1,0],[0,-1],[0,1]],
  moveRandom: function(entity) {
    math.shuffle(this._randomCoords)
    for (let i=0; i < this._randomCoords.length; i++) {
      let nx = entity.x + this._randomCoords[i][0]
      let ny = entity.y + this._randomCoords[i][1]
      if (this.tryMove(entity,nx,ny)) {
        this.move(entity,nx,ny)
        break
      }
    }
  },

  //TODO: creates a lot of garbage! (refactor, send in path etc)
  canSeePlayer: function(entity, maxDistance = 10) {
    const path = []
    math.line(entity.x, entity.y, this.player.x, this.player.y, (x,y) => path.push({x,y}))

    // inSight
    let inSight = true
    for (let i=0; i < path.length; i++) {
      const pos = path[i]
      const lastPos = i > 0 && path[i-1]
      // check if position in path is empty, ignore first (entity) and last (player)
      if (i != 0 && i != (path.length-1) && !this.isEmpty(pos.x,pos.y)) {
        inSight = false
        break
      }

      // check diagonals (in our world, you can't see between corner boxes)
      if (lastPos && lastPos.x !== pos.x && lastPos.y !== pos.y) {
        let dx = lastPos.x - pos.x
        let dy = lastPos.y - pos.y

        inSight = this.isEmpty(pos.x + dx, pos.y) || this.isEmpty(pos.x,pos.y + dy)
        if (!inSight) break
      }
    }

    // path
    return inSight && path.length < maxDistance ? path : false
  },

  //TODO: creates a lot of garbage! (refactor, send in path etc)
  pathBetween: function(mx,my,tx,ty) {
    const passableCallback = (x, y) => {
      if (mx == x && my == y) return true
      return x > 0 && x < this.width && y > 0 && y < this.height && this.isEmpty(x,y)
    }

    const pathFinder = new ROT.Path.AStar(tx, ty, passableCallback, {topology: 4})

    let path = []
    pathFinder.compute(mx, my, (x, y) => path.push({x,y}))
    return path
  },

  pathToPlayer: function(entity) {
    return this.pathBetween(entity.x, entity.y, this.player.x, this.player.y)
  },

  remove: function(entity) {
    this.tiles[posToIndex(entity.x,entity.y)] = false
    entity.hidden = true
  },

  load: function(level,game) {
    this.monsters = []
    this.things = []

    // generate map
    const source = level.map ? level.map : generate(level)

    this.width =  WIDTH
    this.height = HEIGHT
    this.source = source

    let pos = 0
    for(var x = 0, c=''; c = source.charAt(x); x++) {
      if (c == ' ' || c == '\t' || c == '\n') continue;

      let type = sourceToEntity[c]
      let [x,y] = indexToPos(pos++)

      if (type === undefined) {
        console.error('Unknown tile ',type, x,c)
      }

      if (type === ENTITY.PLAYER) {
        this.player = {}
        this.player.x = x
        this.player.y = y
      } else if (type === ENTITY.GRUE || type === ENTITY.SNAKE || type === ENTITY.ZOMBIE || type == ENTITY.EGG) {
        this.monsters.push(createEntity(type,x,y,game))
      } else if (type) {
        //TODO: remove special list of boxes? dead/alive instead?
        this.things.push(createEntity(type,x,y,game))
      }
    }

    if (pos != WIDTH*HEIGHT) {
      throw new Error('bad map')
    }

    // create collision map
    this.tiles = new Array(this.width * this.height)
    this.tiles.fill(false)
    this.tiles[posToIndex(this.player.x,this.player.y)] = this.player
    this.things.forEach(b => this.tiles[posToIndex(b.x,b.y)] = b)
    this.monsters.forEach(m => this.tiles[posToIndex(m.x,m.y)] = m)

    console.log('> Loaded map (map.js)')
    this.loaded = true
  }
}



