export default {
  create: function() {
    return Object.create(this)
  },

  input: function(input) {
    if (input.pause) {
      console.log('next level!')
      input.clear()
      // this.game.hideModal()
      this.game.mode.play()
    }
  },

  update: function() { return null },

  init: function(game) {
    this.game = game

    // create stage
    this.stage = new PIXI.Container();

    // debug
    this.text = new PIXI.Text('Level '+(this.game.level+1)+' \n Press Start to play',
    {font : '34px MiniSet2', fill : 0xaaaaff, align : 'center'});
    this.text.position.x = this.game.renderer.stage.width / 2
    this.text.position.y = this.game.renderer.stage.height / 2
    this.text.anchor.set(0.5,0.5)
    this.stage.addChild(this.text)
  },

  render: function(dt) {
    this.game.renderer.renderer.render(this.stage);
  },

  remove: function() {
    // this.game.renderer.stage.removeChild(this.stage)
  }
}



