import math from './math'
import GameRNG from './alea'
import Simplex from './noise'

// TODO: duplicated in map, move to consts
const WIDTH = 30
const HEIGHT = 19

const defaults = {
  // size
  width: WIDTH,
  height: HEIGHT,
  // player
  // terrain
  box: 0.4,
  heavyBox: 0.1,
  // monsters
  // magic
  playerMinDistance: 3,
}

// this RNG is used to generate seeds for each levels RNG
const LevelsRNG = GameRNG.clone()
// LevelsRNG.setSeed(140108)

export function generate(template) {
  console.log('generate',template)

  // create a level specific RNG
  const rng = GameRNG.clone()

  // remember this levels seed so we can restart
  template.seed = template.seed || LevelsRNG.random(0,801017)
  rng.setSeed(template.seed)

  // mixin defaults
  let level = Object.assign({},defaults,template)

  // make some noise
  const noise = new Simplex(256,rng)

  // place boxes
  let tiles = new Array(level.width)
  for (let x=0; x < level.width; x++) {
    tiles[x] = new Array(level.height)
    for (let y=0; y < level.height; y++) {
      let n = (1 + noise.get(x,y)) / 2 // scale n to 0 to 1
      //console.log('noise',n)
      let tile = '.'
      if (n < level.box) tile = 'b'
      if (n < level.heavyBox) tile = 'B'
      tiles[x][y] = tile
    }
  }

  // place player
  const player = {
    x: rng.randomInt(0,level.width-1),
    y: rng.randomInt(0,level.height-1)
  }

  console.log('> player ',player)
  tiles[player.x][player.y] = '@'

  //TODO: make sure player is not surrounded by HEAVY_BOXes

  // place monsters
  const MONSTERS = ['S','G','Z','E']
  MONSTERS.forEach(type => {
    let count = level.monsters[type] || 0
    while (count > 0) {
      let x = rng.randomInt(0,level.width-1)
      let y = rng.randomInt(0,level.height-1)

      if (math.distance(player.x,player.y,x,y) < level.playerMinDistance) {
        console.log('skipping pos (too close to player)',x,y)
        continue
      }

      // TODO: don't replace monster with monster
      if (!(tiles[x][y] === '.' || tiles[x][y] === 'b')) {
        console.log('skipping pos (occupied)',x,y)
        continue
      }

      tiles[x][y] = type
      count--
    }
  })

  // generate map
  let map = ""
  for (let y=0; y < level.height; y++) {
    for (let x=0; x < level.width; x++) {
      map = map + tiles[x][y]
    }
    map = map + '\n'
  }

  //console.log('map\n',map)

  return map
}



