import {posToIndex} from './map'

import DebugModal from './modals/debug'
import ScreenShake from './effects/screenshake'
import HitEffect from './effects/hit'
import ScoreEffect from './effects/score'

export default {
  pushEntity: function pushEntity (e,dx,dy) {
    const map = this.map
    //console.log('pushEntity ',e.x,e.y,e)

    // try to move e
    let tx = e.x + dx
    let ty = e.y + dy

    // not within map -> squash or false
    if (!map.isWithin(tx,ty)) {
      if (e.isSquashable) {
        e.squash()
        return true
      }
      return false
    }

    // empty -> move if movable ?
    let t = map.entityAt(tx,ty)
    if (!t) {
        // movable ?
        if (!e.isMovable) return false

        // move
        map.move(e,tx,ty)
        return true
    }

    // not empty -> squashable -> squash
    if (e.isSquashable) {
      e.squash()
      return true
    }

    // movable ?
    if (!e.isMovable) return false

    // try to push whatever is in the way
    if (this.pushEntity(t,dx,dy)) {
      map.move(e,tx,ty)
      return true
    } else {
      return false
    }
  },

  movePlayer: function(dx,dy) {
    const map = this.map
    const player = this.player

    const x = player.x + dx
    const y = player.y + dy

    if (!map.isWithin(x,y)) return

    if (map.isEmpty(x,y)) {
      map.move(player,x,y)
    } else {
      const other = map.entityAt(x,y)

      if (other.isMonster) {
        console.log('stupidly pushing the '+other.name)
        this.publish('attack',other,player)
      }

      if (other.isMovable && this.pushEntity(other,dx,dy)) {
        map.move(player,x,y)
      }
    }
  },

  addEffect: function(effect) {
    this.effects = this.effects || []
    this.effects.push(effect)
    this.effectsRunning = this.effectsRunning || 0
    this.effectsRunning += 1
  },

  waitForEffects: function(callback) {
    this.waitingForEffects = this.waitingForEffects || []
    this.waitingForEffects.push(callback)
  },

  // --- Game loop methods

  // TODO: This is only called once every loop, not for every tick!!
  playerInput: function() {
    if (this.modal) return this.modal.input(this.input)

    const player = this.player
    const input = this.input

    // handle movement input
    if (!this.player.isMoving) {
      if (input.right) { this.movePlayer( 1 ,  0) }
      if (input.left)  { this.movePlayer(-1 ,  0) }
      if (input.up)    { this.movePlayer( 0 , -1) }
      if (input.down)  { this.movePlayer( 0 ,  1) }
    }

    // game state input
    if (input.pause) { this.mode.pause() }

    // debug stuff
    if (input.key == 68 /* D */) { this.mode.pause(DebugModal.create()) }
    if (input.key == 49 /* 1 */) { this.map.monsters.forEach(m => m.kill()) }
    if (input.key == 50 /* 2 */) { this.mode.load(this.level) }
    if (input.key == 51 /* 3 */) { this.mode.load(this.level) }
    if (input.key == 57 /* 3 */) { this.addEffect(ScreenShake.create(0.3,16)) }

    // clear input
    input.clear()
  },

  update: function(step) {
    // modal?
    if (this.modal) return this.modal.update(step)

    // is anyone waiting to be notified when effecs are done?
    if (this.waitingForEffects && this.effectsRunning == 0) {
      let callback = null
      while(callback = this.waitingForEffects.shift()) {
        callback()
      }
      this.waitingForEffects = null
    }

    //this.debug.updateStep = step
    this.debug.updateFrames = this.debug.updateFrames || 0
    this.debug.updateFrames++

    // Status
    this.totalTimeSpent += step
    this.levelTimeSpent += step

    // time's up?
    if (this.levelTimeSpent > (this.levelMaxTime + this.levelTimeBonus)) {
      this.mode.die('Too slow, time is precious!')
      return
    }

    // handle monsters (move this )
    for (let i=0; i < this.map.monsters.length; i++) {
      let monster = this.map.monsters[i]
      if (monster.update) monster.update(step)
    }
  },

  render: function(dt) {
    if (this.modal) return this.modal.render(dt)

    //this.debug.renderDt = dt
    this.debug.renderFrames = this.debug.renderFrames || 0
    this.debug.renderFrames++

    this.renderer.render(dt)
  },

  // --- Game events
  // TODO: refactor to a table with bindings instead?
  // TODO: refactor to mixin + compose (initXXX) fn instead?
  init: function() {
    console.log('init (game-play.js)')

    this.subscribe('explosion', ({x,y}) => {
      console.log('kaboom!',x,y)
      // remove a random cell
      let e = this.map.entityAt(x+1,y)
      if (e) {
        this.map.remove(e)
      }

      // effects
      this.addEffect(ScreenShake.create(0.3,16))
    })

    this.subscribe('kill',(monster,score) => {
      console.log('kill listener',monster.isDead,monster.x,monster.y,this.map.entityAt(monster.x,monster.y))
      // update player score
      this.score += score

      // add time bonus if any
      if (this.levelTimeSpent - this.lastKillTime < 1) {
        this.multipleKillCounter += 1
      } else {
        this.multipleKillCounter = 1
      }
      this.lastKillTime = this.levelTimeSpent

      if (this.multipleKillCounter > 1) {
        let bonus = this.multipleKillCounter * 500
        this.score += bonus
        this.addEffect(ScoreEffect.create(this.player,bonus))
        console.log('multi kill bonus!',this.multipleKillCounter,bonus)
      }

      // add some bonus time
      this.levelTimeBonus += 10

      // effects
      this.addEffect(ScoreEffect.create(monster,score))

      // check if there are any monsters left
      let monstersAlive = 0
      for (let i=0; i < this.map.monsters.length; i++) {
        if (!this.map.monsters[i].isDead) monstersAlive++
      }

      // level complete?
      if (monstersAlive == 0) this.waitForEffects(() => {
        console.log('no more monsters! WIN!',this.debug.updateFrames)
        this.mode.load(this.level + 1)
      })
    })

    this.subscribe('attack', (attacker,target) => {
      // decide if it's a hit or miss
      // do damage
      console.log(attacker.name,'hits',target.name)
      target.hp = target.hp - 1

      // effects
      this.addEffect(HitEffect.create())

      // check if player is dead
      if (target.hp === 0) {
        this.mode.die('Killed by a '+attacker.name)
      }
    })
  }
}



