// mix-ins
export function compose(components) {
  var args = (arguments.length === 1?[arguments[0]]:Array.apply(null, arguments));
  let inits = args.reduce((cs,c) => c.init ? cs.concat(c.init) : cs,[])
  let obj = Object.assign.apply(null,args)

  obj.init = function() {
    inits.forEach(init => init.apply(this,arguments))
  }

  return obj
}



