console.log('Loading GameLoop (index.js)')

function timestamp() {
  return window.performance && window.performance.now ? window.performance.now() : new Date().getTime();
}

export const makeGameloop = function makeGameloop(options) {
  console.log('Creating a new gameloop',options)
  let now,
      dt       = 0,
      last     = timestamp(),
      slow     = options.slow || 1, // slow motion scaling factor
      step     = 1/options.fps,
      slowStep = slow * step,
      update   = options.update,
      render   = options.render,
      playerInput = options.input,
      fpsmeter = new FPSMeter(options.fpsmeter || { decimals: 0, graph: true, theme: 'dark', left: 'auto', right: '5px' });

  function frame() {
    fpsmeter.tickStart();
    now = timestamp();

    //TODO: should input really be seperate?
    playerInput()

    dt = dt + Math.min(1, (now - last) / 1000);
    while(dt > slowStep) {
      dt = dt - slowStep;
      update(step);
    }

    // TODO: why the fuck to this make yanky animations??
    //render(dt/slow);
    render(step);

    last = now;
    fpsmeter.tick();
    requestAnimationFrame(frame, options.canvas);
  }

  requestAnimationFrame(frame);
}



